'use client';

import { useState, useEffect } from 'react';
import { Authenticator } from '@aws-amplify/ui-react';
import { generateClient } from 'aws-amplify/data';
import type { Schema } from '../../../amplify/data/resource';

const client = generateClient<Schema>();

interface TimeSlotConfig {
  id: string;
  timeSlot: string;
  maxCapacity: number;
  currentRegistrations: number;
  isActive: boolean;
}

interface Registration {
  id: string;
  firstName: string;
  lastName: string;
  email: string;
  phone: string;
  timeSlot: string;
  numberOfKids: number;
  needsChildcare: boolean;
  referredBy?: string;
  registrationDate: string;
}

const TIME_SLOTS = [
  '09:00',
  '09:30',
  '10:00',
  '10:30',
  '11:00',
  '11:30'
];

function AdminDashboard() {
  const [timeSlots, setTimeSlots] = useState<TimeSlotConfig[]>([]);
  const [registrations, setRegistrations] = useState<Registration[]>([]);
  const [inviteEmail, setInviteEmail] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    try {
      // Load time slot configurations
      const { data: timeSlotData } = await client.models.TimeSlotConfig.list();
      setTimeSlots(timeSlotData as TimeSlotConfig[]);

      // Load registrations
      const { data: registrationData } = await client.models.Registration.list();
      setRegistrations(registrationData as Registration[]);
    } catch (error) {
      console.error('Error loading data:', error);
    }
  };

  const initializeTimeSlots = async () => {
    setLoading(true);
    try {
      // Check if time slots already exist
      const { data: existingSlots } = await client.models.TimeSlotConfig.list();
      
      if (existingSlots.length === 0) {
        // Create default time slots with capacity of 10 each
        const promises = TIME_SLOTS.map(slot =>
          client.models.TimeSlotConfig.create({
            timeSlot: slot,
            maxCapacity: 10,
            currentRegistrations: 0,
            isActive: true
          })
        );
        
        await Promise.all(promises);
        setMessage('Time slots initialized successfully!');
        loadData();
      } else {
        setMessage('Time slots already exist.');
      }
    } catch (error) {
      console.error('Error initializing time slots:', error);
      setMessage('Error initializing time slots.');
    } finally {
      setLoading(false);
    }
  };

  const updateTimeSlotCapacity = async (id: string, newCapacity: number) => {
    try {
      await client.models.TimeSlotConfig.update({
        id,
        maxCapacity: newCapacity
      });
      
      setTimeSlots(prev => prev.map(slot => 
        slot.id === id ? { ...slot, maxCapacity: newCapacity } : slot
      ));
      
      setMessage('Time slot capacity updated successfully!');
    } catch (error) {
      console.error('Error updating capacity:', error);
      setMessage('Error updating capacity.');
    }
  };

  const generateInviteLink = async () => {
    if (!inviteEmail.trim()) {
      setMessage('Please enter an email address.');
      return;
    }

    setLoading(true);
    try {
      const token = Math.random().toString(36).substr(2, 15);
      
      await client.models.InviteLink.create({
        token,
        email: inviteEmail,
        isUsed: false,
        createdAt: new Date().toISOString()
      });

      const inviteUrl = `${window.location.origin}/register/${token}`;
      
      navigator.clipboard.writeText(inviteUrl);
      setMessage(`Invite link generated and copied to clipboard: ${inviteUrl}`);
      setInviteEmail('');
    } catch (error) {
      console.error('Error generating invite link:', error);
      setMessage('Error generating invite link.');
    } finally {
      setLoading(false);
    }
  };

  const exportRegistrations = () => {
    const csvContent = [
      ['Name', 'Email', 'Phone', 'Time Slot', 'Number of Kids', 'Needs Childcare', 'Referred By', 'Registration Date'],
      ...registrations.map(reg => [
        `${reg.firstName} ${reg.lastName}`,
        reg.email,
        reg.phone,
        reg.timeSlot,
        reg.numberOfKids.toString(),
        reg.needsChildcare ? 'Yes' : 'No',
        reg.referredBy || '',
        new Date(reg.registrationDate).toLocaleDateString()
      ])
    ].map(row => row.map(cell => `"${cell}"`).join(',')).join('\n');

    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    const url = URL.createObjectURL(blob);
    link.setAttribute('href', url);
    link.setAttribute('download', 'christmas-store-registrations.csv');
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <div className="max-w-6xl mx-auto p-6">
      <h1 className="text-3xl font-bold mb-6 text-red-600">Christmas Store Admin Dashboard</h1>
      
      {message && (
        <div className="mb-4 p-4 bg-blue-50 text-blue-600 rounded-lg">
          {message}
        </div>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        {/* Time Slot Management */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-2xl font-semibold mb-4">Time Slot Management</h2>
          
          {timeSlots.length === 0 ? (
            <div>
              <p className="mb-4 text-gray-600">No time slots configured yet.</p>
              <button
                onClick={initializeTimeSlots}
                disabled={loading}
                className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700 disabled:opacity-50"
              >
                {loading ? 'Initializing...' : 'Initialize Time Slots'}
              </button>
            </div>
          ) : (
            <div className="space-y-4">
              {timeSlots.map(slot => (
                <div key={slot.id} className="flex items-center justify-between p-4 border rounded-lg">
                  <div>
                    <span className="font-medium">{slot.timeSlot}</span>
                    <span className="text-gray-600 ml-2">
                      ({slot.currentRegistrations}/{slot.maxCapacity} registered)
                    </span>
                  </div>
                  <div className="flex items-center space-x-2">
                    <input
                      type="number"
                      value={slot.maxCapacity}
                      onChange={(e) => updateTimeSlotCapacity(slot.id, parseInt(e.target.value) || 0)}
                      className="w-20 px-2 py-1 border rounded"
                      min="0"
                    />
                    <span className="text-sm text-gray-500">max</span>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Invite Link Generator */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-2xl font-semibold mb-4">Generate Invite Link</h2>
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Email Address (optional)
              </label>
              <input
                type="email"
                value={inviteEmail}
                onChange={(e) => setInviteEmail(e.target.value)}
                placeholder="recipient@example.com"
                className="w-full p-2 border border-gray-300 rounded-md"
              />
            </div>
            <button
              onClick={generateInviteLink}
              disabled={loading}
              className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700 disabled:opacity-50"
            >
              {loading ? 'Generating...' : 'Generate Invite Link'}
            </button>
          </div>
        </div>
      </div>

      {/* Registration Summary */}
      <div className="bg-white rounded-lg shadow-md p-6 mt-8">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-2xl font-semibold">Registration Summary</h2>
          <button
            onClick={exportRegistrations}
            className="bg-purple-600 text-white px-4 py-2 rounded hover:bg-purple-700"
          >
            Export CSV
          </button>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
          <div className="bg-blue-50 p-4 rounded-lg">
            <h3 className="font-semibold text-blue-800">Total Registrations</h3>
            <p className="text-2xl font-bold text-blue-600">{registrations.length}</p>
          </div>
          <div className="bg-green-50 p-4 rounded-lg">
            <h3 className="font-semibold text-green-800">Total Children</h3>
            <p className="text-2xl font-bold text-green-600">
              {registrations.reduce((sum, reg) => sum + reg.numberOfKids, 0)}
            </p>
          </div>
          <div className="bg-yellow-50 p-4 rounded-lg">
            <h3 className="font-semibold text-yellow-800">Need Childcare</h3>
            <p className="text-2xl font-bold text-yellow-600">
              {registrations.filter(reg => reg.needsChildcare).length}
            </p>
          </div>
        </div>

        <div className="overflow-x-auto">
          <table className="min-w-full table-auto">
            <thead>
              <tr className="bg-gray-50">
                <th className="px-4 py-2 text-left">Name</th>
                <th className="px-4 py-2 text-left">Email</th>
                <th className="px-4 py-2 text-left">Phone</th>
                <th className="px-4 py-2 text-left">Time Slot</th>
                <th className="px-4 py-2 text-left">Kids</th>
                <th className="px-4 py-2 text-left">Childcare</th>
              </tr>
            </thead>
            <tbody>
              {registrations.map(reg => (
                <tr key={reg.id} className="border-t">
                  <td className="px-4 py-2">{reg.firstName} {reg.lastName}</td>
                  <td className="px-4 py-2">{reg.email}</td>
                  <td className="px-4 py-2">{reg.phone}</td>
                  <td className="px-4 py-2">{reg.timeSlot}</td>
                  <td className="px-4 py-2">{reg.numberOfKids}</td>
                  <td className="px-4 py-2">{reg.needsChildcare ? 'Yes' : 'No'}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

export default function AdminPage() {
  return (
    <Authenticator>
      {({ signOut, user }) => (
        <div className="min-h-screen bg-gray-50">
          <div className="bg-white shadow-sm">
            <div className="max-w-6xl mx-auto px-6 py-4 flex justify-between items-center">
              <h1 className="text-xl font-semibold">Welcome, {user?.signInDetails?.loginId}</h1>
              <button
                onClick={signOut}
                className="bg-red-600 text-white px-4 py-2 rounded hover:bg-red-700"
              >
                Sign Out
              </button>
            </div>
          </div>
          <div className="py-8">
            <AdminDashboard />
          </div>
        </div>
      )}
    </Authenticator>
  );
}